.podcasts {
  padding: 94px 0;
  background-color: #E6E8EC;

  @include _1024 {
    padding-bottom: 92px;
  }

  @include _768 {
    padding-bottom: 94px;
  }

  @include _320 {
    padding: 54px 0;
  }

  &__title {
    margin-bottom: 30px;

    @include _768 {
      margin-bottom: 32px;
    }

    @include _320 {
      margin-bottom: 20px;
      font-size: 24px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 30px;
    margin-bottom: 60px;

    @include _768 {
      grid-template-columns: 1fr;
    }

    @include _1024 {
      margin-bottom: 58px;
    }

    @include _320 {
      margin-bottom: 30px;
    }
  }

  &__item {
    background-color: #fff;

    &:nth-child(n + 9) {
      display: none;
    }

    @include _320 {
      &:nth-child(n+5) {
        display: none;
      }
    }
  }

  &__card {
    display: flex;
    max-width: 540px;
    width: 100%;
    height: 100%;

    @include _320 {
      flex-direction: column;
    }
  }

  &__card-prew {
    position: relative;
    max-width: 112px;
    width: 100%;

    @include _320 {
      max-width: 256px;
    }
  }

  &__cover-date {
    position: absolute;
    top: 125px;
    left: 0;
    padding: 3px 8.5px;
    background-color: #E6E8EC;
    font-weight: 400;
    font-size: 10px;
    line-height: 10px;
    color: #121723;

    @include _320 {
      top: 0;
    }
  }

  &__card-body {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }

  &__card-top {
    display: flex;
    align-items: center;
    padding: 24px 30px;
    border-bottom: 1px solid #E6E8EC;

    @include _320 {
      padding: 17px 15.25px 16px 16px;
    }
  }

  &__card-left {
    margin-right: auto;
  }

  &__timer {
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: #121723;
  }

  &__composition {
    margin-bottom: 4px;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #121723;

    @include _320 {
      margin-bottom: 5px;
      font-size: 16px;
      line-height: 16px;
    }
  }

  &__executor {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #A1A6B4;

    @include _320 {
      font-size: 12px;
      line-height: 12px;
    }
  }

  &__card-botom {
    display: flex;
    align-items: center;
    padding: 7px 30px;

    @include _320 {
      padding: 5px 35px;
    }
  }

  &__stats {
    font-size: 16px;
    line-height: 16px;
    color: #A1A6B4;
    transition: color .3s ease-in-out;

    @include _320 {
      font-size: 12px;
      line-height: 12px;
    }

    &:not(:last-child) {
      margin-right: 30px;
    }

    svg {
      margin-right: 2px;

      path {
        transition: fill .3s ease-in-out;
      }
    }

    &:hover {
      color: $purp;

      path {
        fill: $purp;
      }
    }

    &:focus-visible {
      background-color: $litepurp;
      color: #fff;

      path {
        fill: #fff;
      }
    }

    &:active {
      color: $darkpurp;

      path {
        fill: $darkpurp;
      }
    }
  }

  &__btn-play {
    @include _320 {
      display: flex;
      align-items: center;
      width: 36px;
      height: 36px;
    }

    svg {
      width: 100%;

      circle,
      path {
        transition: all .3s ease-in-out;
      }
    }

    &:hover {
      circle {
        stroke: $purp;
      }

      path {
        fill: $purp;
      }
    }

    &:focus-visible {
      circle {
        stroke: $litepurp;
      }

      path {
        fill: $litepurp;
      }
    }

    &:active {
      circle {
        stroke: $darkpurp;
      }

      path {
        fill: $darkpurp;
      }
    }
  }

  &__btn {
    padding: 24px 52px;
    border: 2px solid #6D31EE;
    border-radius: 40px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: #6D31EE;
    transition: all .3s ease-in-out;

    @include _320 {
      padding: 15px 28px;
      font-size: 16px;
      line-height: 16px;
    }

    &:hover {
      background-color: $purp;
      color: #fff;
    }

    &:focus-visible {
      background-color: $litepurp;
      color: #fff;
    }

    &:active {
      background-color: $darkpurp;
      color: #fff;
    }
  }

  &__btn--hidden {
    display: none;
  }
}

.podcasts__item.podcasts__item--visible {
  display: block;
}
