.hero {
  padding: 38px 0;

  @include _1024 {
    padding: 90px 0;
  }

  @include _320 {
    padding: 40px 0;
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__title {
    margin-bottom: 14px;
    font-weight: 700;
    font-size: 60px;
    line-height: 100%;
    color: #121723;

    span {
      font-family: 'Muller-Italic';
      font-weight: 400;
      font-style: italic;
    }

    @include _320 {
      margin-bottom: 8px;
      font-size: 32px;
      line-height: 100%;
    }
  }

  &__descr {
    margin-bottom: 45px;
    width: 635px;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #121723;

    a {
      color: $purp;
    }

    @include _1024 {
      width: 539px;
      margin-bottom: 20px;
    }

    @include _320 {
      width: 260px;
      font-size: 14px;
      line-height: 150%;
    }
  }

  &__btn {
    padding: 25px 54px;
    background: #6D31EE;
    border-radius: 40px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: #fff;
    transition: all .3s ease-in-out;

    @include _320 {
      padding: 17px 45px;
      font-size: 16px;
      line-height: 16px;
    }

    &:hover {
      background-color: $litepurp;
    }

    &:focus-visible {
      background-color: $litepurp;
      outline: 2px solid $darkpurp;
    }

    &:active {
      background-color: $darkpurp;
    }
  }

  &__right {
    @include _768 {
      display: none;
    }
  }
}
