.guests {
  padding-top: 94px;
  padding-bottom: 91px;
  background-color: #121723;

  @include _320 {
    padding-top: 54px;
    padding-bottom: 54px;
  }

  &__title {
    margin-bottom: 12px;
    color: #fff;

    @include _320 {
      font-size: 24px;
      line-height: 100%;
    }
  }

  &__comes {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: #A1A6B4;

    @include _320 {
      margin-bottom: 0;
      font-size: 14px;
    }
  }

  &__descr {
    max-width: 825px;
    margin-bottom: 50px;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: #A1A6B4;

    @include _1024 {
      width: 536px;
    }

    @include _768 {
      margin-bottom: 53px;
    }

    @include _320 {
      width: 256px;
      margin-bottom: 30px;
      font-size: 14px;

    }
  }

  &__content {
    display: flex;
    justify-content: space-between;

    @include _768 {
      flex-direction: column;
    }
  }

  &__content-left {
    flex: 1 1 730px;
    margin-right: 30px;

    @include _768 {
      flex: 0 0 auto;
      margin: 0px 0px 53px 0px;
    }

    @include _320 {
      margin-bottom: 30px;
    }
  }

  &__list {
    border-bottom: 1px solid #a1a6b3;
  }

  &__item {
    cursor: pointer;

    @include _320 {
      &:nth-child(5) {
        display: none;
      }
    }
  }

  &__item-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: -1px;
    padding-top: 24px;
    padding-bottom: 23px;
    border-top: 1px solid #a1a6b3;
    border-bottom: 1px solid transparent;
    transition: all .3s ease-in-out;

    &:focus-visible {
      background-color: rgba(170, 157, 250, 0.3);
    }

    &:hover {
      h3 {
        color: $litepurp;
      }

      button {
        border-color: $litepurp;

        span::after {
          border-color: $litepurp;
        }
      }
    }

    &:active {
      h3 {
        color: $litepurp;
      }

      button {
        border-color: $litepurp;
        background-color: $litepurp;

        span::after {
          border-color: #fff;
        }
      }
    }

    @include _320 {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }

  &__subtitle {
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: #fff;
    transition: color .3s ease-in-out;

    @include _320 {
      font-size: 16px;
      line-height: 16px;
    }
  }

  &__item-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 42px;
    height: 41px;
    padding: 16px 13px;
    border: 2px solid #a1a6b3;
    border-radius: 50%;
    transition-property: border background;
    transition-duration: .3s;
    transition-timing-function: ease-in-out;

    @include _320 {
      width: 26px;
      height: 26px;
      padding: 0;
    }
  }

  &__arrow {
    position: relative;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 13px;
      height: 13px;
      border-top: 2px solid #a1a6b3;
      border-left: 2px solid #a1a6b3;
      transform: translateY(-60%) translateX(45%) rotate(-135deg);
      transition: border .3s ease-in-out;

      @include _320 {
        width: 10px;
        height: 10px;
      }
    }
  }

  &__item-bottom {
    padding-top: 30px;
    padding-bottom: 30px;

    @include _320 {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }

  &__card-list {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(4, 1fr);
    column-gap: 29px;
    row-gap: 17px;

    @include _1024 {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(5, 1fr);
    }

    @include _768 {
      column-gap: 31px;
      row-gap: 30px;
    }

    @include _320 {
      column-gap: 30px;
      row-gap: 27px;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(8, 1fr);
    }
  }

  &__card-item {
    @include _1024 {
      &:nth-child(13) {
        grid-column: 1/4;
        grid-row: 5;
      }

      &:nth-child(14) {
        grid-column: 2/4;
        grid-row: 5;
      }

      &:nth-child(15) {
        grid-column: 3/4;
        grid-row: 5;
      }
    }

    @include _320 {
      &:nth-child(5) {
        grid-column: 2;
        grid-row: 1;
      }

      &:nth-child(6) {
        grid-column: 2;
        grid-row: 2;
      }

      &:nth-child(7) {
        grid-column: 2;
        grid-row: 3;
      }

      &:nth-child(8) {
        grid-column: 2;
        grid-row: 4;
      }

      &:nth-child(11) {
        grid-column: 1;
        grid-row: 7;
      }

      &:nth-child(13) {
        grid-column: 1;
        grid-row: 5;
      }

      &:nth-child(14) {
        grid-column: 2;
        grid-row: 5;
      }

      &:nth-child(15) {
        grid-column: 1;
        grid-row: 8;
      }
    }
  }

  &__card-name {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #fff;
    transition: color .3s ease-in-out;

    &:hover {
      color: $purp;
    }

    &:focus-visible {
      color: $litepurp;
    }

    &:active {
      color: $darkpurp;
    }

    @include _320 {
      padding-right: 13px;
      font-size: 14px;
      line-height: 110%;
    }
  }

  &__content-right {
    flex: 1 1 350px;

    @include _768 {
      flex: 0 0 auto;
    }
  }

  &__card-prew {
    margin-bottom: 14px;

    @include _768 {
      margin-bottom: 9px;
    }

    @include _320 {
      margin-bottom: 13px;
    }
  }

  &__social-list {
    display: flex;
    margin-bottom: 8px;
    gap: 30px;

    path {
      transition: fill .3s ease-in-out;
    }

    a {
      cursor: pointer;

      &:hover {
        path {
          fill: $purp;
        }
      }

      &:active {
        path {
          fill: $darkpurp;
        }
      }

      &:focus-visible {
        path {
          fill: $litepurp;
        }
      }
    }

    @include _320 {
      gap: 21px;
    }
  }

  &__social-item {
    @include _320 {
      svg {
        width: 36px;
        height: 36px;
      }
    }
  }

  &__name {
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: #fff;
  }

  &__bio {
    max-width: 350px;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 16px;
    line-height: 180%;
    color: #A1A6B4;

    @include _768 {
      max-width: 390px;
    }
  }

  &__btn {
    padding: 11px 18.5px;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: #AA9DFA;
    border: 2px solid #AA9DFA;
    border-radius: 77px;
    transition: all .3s ease-in-out;

    &:hover {
      color: #fff;
      background-color: $litepurp;
      border-color: $litepurp;
    }

    &:focus {
      color: #fff;
      background-color: $purp;
      border-color: $purp;
    }

    &:active {
      color: #fff;
      background-color: $darkpurp;
      border-color: $darkpurp;
    }

    @include _320 {
      padding: 8.5px 13.5px;
    }
  }
}


.guests__item-top.ui-state-active .guests__item-btn {
  transform: rotate(-180deg);
}


.guests__item-top.ui-state-active {
  border-color: $litepurp;
}

.guests__item-top.ui-state-active+.guests__item-bottom {
  border-bottom: 1px solid $litepurp;
  position: relative;
  z-index: 2;
}

.tabs-item {
  display: none;
}

.tabs-item--active {
  display: block;
}
