.playlists {
  padding-top: 94px;
  padding-bottom: 94px;

  @include _320 {
    padding-top: 54px;
    padding-bottom: 54px;
  }

  &__title {
    margin-bottom: 12px;

    @include _320 {
      margin-bottom: 20px;
      font-weight: 400;
      font-size: 24px;
      line-height: 100%;
      color: #121723;
    }
  }

  &__content {
    display: flex;
    justify-content: space-between;

    @include _1024 {
      flex-direction: column;
    }
  }

  &__left {
    flex: 0 0 350px;
    margin-right: 30px;

    @include _1024 {
      flex: 0 0 auto;
    }

    @include _768 {
      margin-bottom: 42px;
    }

    @include _320 {
      margin-bottom: 30px;
      position: relative;
    }
  }

  &__left,
  &__cust-radio {
    display: flex;
    flex-direction: column;
  }

  &__custrad-list {
    display: grid;
    grid-auto-flow: row;
    gap: 16px;
    margin-bottom: 29px;
    padding-left: 2px;

    @include _1024 {
      display: grid;
      grid-auto-flow: column;
      column-gap: 30px;
      grid-template-columns: 138px 330px;
      grid-template-rows: repeat(4, 1fr);
    }

    @include _768 {
      gap: 30px;
      margin-bottom: auto;
    }

    @include _320 {
      display: none;
    }
  }

  &__carousel {
    display: none;

    @include _320 {
      display: block;
    }
  }

  &__genre {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #A1A6B4;

    @include _320 {
      margin-bottom: 6px;
      font-size: 12px;
      line-height: 12px;
    }
  }

  &__radio {
    margin-bottom: 16px;
  }

  &__radiobox {
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #121723;
  }

  &__copyrights {
    padding-top: 28px;
    border-top: 1px solid #d2d5dd;

    @include _1024 {
      display: none;
    }
  }

  &__copy {
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #A1A6B4;

    @include _320 {
      font-size: 12px;
      line-height: 16px;
    }
  }

  &__copy--italic {
    margin-bottom: 6px;
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
    color: #A1A6B4;

    @include _320 {
      font-size: 12px;
    }
  }

  &__png {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: 30px;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #A1A6B4;
    border: 1px solid #A1A6B4;
    border-radius: 50%;
  }

  &__right {
    padding-top: 14px;

    @include _1024 {
      padding: 0;
      margin-bottom: 49px;
    }

    @include _320 {
      margin-bottom: 41px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 30px;

    @include _1024 {
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(4, 1fr);
    }

    @include _768 {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(6, 1fr);
    }

    @include _320 {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(6, 159px);
    }
  }

  &__item {
    position: relative;

    :hover {
      .playlists__card-content {
        background-color: $purp;
        border-color: $purp;
      }

      h4,
      p {
        color: #fff;
      }

      .playlists__card-prew {
        filter: brightness(50%);
      }

      .playlists__btn-play {
        opacity: 1;
      }
    }

    :focus-visible {
      .playlists__card-content {
        background-color: $litepurp;
        border-color: $litepurp;
      }

      h4,
      p {
        color: #fff;
      }
    }

    :active {
      .playlists__card-content {
        background-color: $darkpurp;
        border-color: $darkpurp;
      }

      h4,
      p {
        color: #fff;
      }
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
    cursor: pointer;

    @include _1024 {
      flex-direction: row;
    }

    @include _320 {
      flex-direction: column;
    }
  }

  &__card-prew {
    transition: all .3s ease-in-out;
  }

  &__btn-play {
    opacity: 0;
    position: absolute;
    z-index: 2;
    top: 25%;
    left: 40%;
    width: 42px;
    height: 42px;
    background: url('/img/play_btn.svg') center center no-repeat;
    transition: all .3s ease-in-out;

    @include _1024 {
      top: 30%;
      left: 14%;
    }

    @include _320 {
      top: 20%;
      left: 30%;
    }
  }

  &__item-lable {
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 20px;

    @include _1024 {
      left: 0;
      right: auto;
    }

    @include _320 {
      left: auto;
      right: 0;
    }
  }

  &__card-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    padding: 15px;
    border: 1px solid #E6E8EC;
    transition: all .3s ease-in-out;

    @include _1024 {
      justify-content: center;
      padding: 10px;
    }

    @include _320 {
      padding: 10px;
    }
  }

  &__card-title {
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: #121723;
    transition: color .3s ease-in-out;

    @include _1024 {
      width: 120px;
    }

    @include _320 {
      margin-bottom: 5px;
      width: 90px;
      font-size: 12px;
      line-height: 12px;
    }
  }

  &__card-desc {
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: #A1A6B4;
    transition: color .3s ease-in-out;

    @include _1024 {
      width: 120px;
    }

    @include _320 {
      width: 90px;
      font-size: 9px;
      line-height: 9px;
    }
  }

  &__copyrights--down {
    display: none;

    @include _1024 {
      display: block;
      padding-top: 29px;
      border-top: 1px solid #d2d5dd;
    }

    @include _320 {
      padding-top: 20px;
    }
  }

  &__swiper {
    display: none;

    @include _320 {
      position: relative;
      display: block;
      max-width: 255px;
      height: 35px;
    }
  }

  &__swiper-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    padding: 0 19.5px;
    border: 2px solid $purp;
    border-radius: 77px;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: $purp;

    &:hover {
      color: #fff;
      background-color: $purp;
      border-color: $purp;
    }
  }
}

// radio box

.playlists__radio>input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.playlists__radio>span {
  display: inline-flex;
  align-items: center;
}

.playlists__radio>span::before {
  content: '';
  display: inline-block;
  width: 26px;
  height: 26px;
  flex: 0 0 auto;
  border: 2px solid #A1A6B4;
  border-radius: 50%;
  margin-right: 14px;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 50% 50%;
  transition: all .3s ease-in-out;
}

/* стили при наведении курсора на радио */
.playlists__radio>input:not(:disabled):not(:checked)+span:hover::before {
  border-color: #6D31EE;
}

/* стили для активной радиокнопки (при нажатии на неё) */
.playlists__radio>input:not(:disabled):active+span::before {
  background-color: #6D31EE;
  border-color: #A1A6B4;
}

/* стили для радиокнопки, находящейся в фокусе */
.playlists__radio:focus-visible>input+span::before {
  box-shadow: 0 0 0 0.2rem rgba($color: #6D31EE, $alpha: .5);
}

/* стили для радиокнопки, находящейся в фокусе и не находящейся в состоянии checked */
.playlists__radio:focus-visible>input:not(:checked)+span::before {
  background-color: #D2D5DD;
  border: 2px solid #6D31EE;
}

/* стили для радиокнопки, находящейся в состоянии checked */
.playlists__radio>input:checked+span::before {
  background: url('/img/checked.svg') center center;
  border-color: #6D31EE;
}
