.adv {
  padding-top: 94px;
  padding-bottom: 131px;
  background-color: #292E39;

  @include _1024 {
    padding-bottom: 94px;
  }

  @include _768 {
    padding-top: 54px;
    padding-bottom: 94px;
  }

  @include _320 {
    padding-bottom: 54px;
  }

  &__content {
    display: flex;

    @include _768 {
      flex-direction: column;
      align-items: center;
    }
  }

  &__left-content {
    margin-right: 30px;
    flex: 1 1 640px;

    @include _1024 {
      display: flex;
      flex: 1 1 397px;
      padding-top: 116px;
    }

    @include _768 {
      margin-right: auto;
      margin-bottom: 50px;
      padding-top: 0;
    }

    @include _320 {
      margin-bottom: 36px;
      flex: 0 0 auto;
    }
  }

  &__right-content {
    flex: 1 1 445px;

    @include _1024 {
      flex: 1 1 397px;
    }

    @include _320 {
      flex: 0 0 auto;
    }
  }

  &__title {
    max-width: 441px;
    margin-bottom: 19px;
    color: #F0D288;

    @include _320 {
      margin-bottom: 12px;
      font-weight: 400;
      font-size: 24px;
      line-height: 100%;
    }
  }

  &__wave {
    width: 100%;
    height: 10px;
    margin-bottom: 19px;
    background: url('/img/wave.svg') center center no-repeat;
    background-size: cover;

    @include _768 {
      background-image: url('/img/wave-768.svg');
    }

    @include _320 {
      margin-bottom: 19px;
      background-image: url('/img/wave-320.svg');
    }
  }

  &__descr,
  &__subdescr {
    font-weight: 400;
    font-size: 16px;
    line-height: 180%;
    color: #fff;

    @include _320 {
      font-weight: 400;
      font-size: 14px;
      line-height: 160%;
      color: #fff;
    }
  }

  &__descr {
    margin-bottom: 10px;
  }

  &__subdescr {
    margin-bottom: 30px;

    @include _320 {
      margin-bottom: 10px;
    }
  }

  &__subdescr-color {
    color: #F0D288;
  }

  &__cost {
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 30px;
    line-height: 30px;
    color: #fff;

    @include _320 {
      margin-bottom: 17px;
      font-weight: 500;
      font-size: 24px;
      line-height: 24px;
      color: #fff;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 300px;
    width: 100%;
    padding-top: 25px;
    padding-bottom: 25px;
    font-weight: 500;
    font-size: 20px;
    line-height: 100%;
    color: #F0D288;
    border: 2px solid #F0D288;
    border-radius: 100px;
    transition: all .3s ease-in-out;

    &:hover {
      background-color: #F0D288;
      color: #121723;

      span {
        background-image: url('/img/adv-btn-img__hover.svg');
      }
    }

    &:focus-visible {
      background-color: #BB973E;
      color: #121723;

      span {
        background-image: url('/img/adv-btn-img.svg');
      }
    }

    &:active {
      background-color: #BB973E;
      border-color: #BB973E;
      color: #121723;

      span {
        background-image: url('/img/adv-btn-img__active.svg');
      }
    }

    @include _768 {
      padding-top: 23px;
      padding-bottom: 23px;
    }

    @include _320 {
      font-weight: 500;
      font-size: 16px;
      line-height: 100%;
      color: #F0D288;
      padding: 14px 0;
    }
  }

  &__btn-img {
    margin-right: 8px;
    display: block;
    width: 20px;
    height: 20px;
    background: url('/img/adv-btn-img.svg') center center no-repeat;
    background-size: cover;
    transition: all .3s ease-in-out;
  }
}
