.header {
  &__ahead {
    display: none;

    @include _1024 {
      display: block;
      padding: 23px 0;
      background-color: #F2F2F2;
    }

    @include _320 {
      padding: 9px;
    }
  }

  &__ahead-logo {
    @include _1024 {
      width: 112px;
    }

    @include _320 {
      width: 87px;
    }
  }

  &__ahead-btn {
    @include _1024 {
      display: none;
    }

    @include _320 {
      display: block;
    }
  }

  &__top {
    padding: 20px 0;

    @include _320 {
      padding: 12px;
    }
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    @include _1024 {
      display: none;
    }
  }

  &__menu-nav {
    display: flex;
    align-items: center;
  }

  &__menu-list {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include _768 {
      display: none;
    }
  }

  &__menu-item {
    &:not(:last-child) {
      margin-right: 59px;
    }

    a {
      font-weight: 400;
      font-size: 20px;
      line-height: 20px;
      color: #121723;
      transition: all .3s ease-in-out;

      &:hover {
        color: $purp;
      }

      &:focus-visible {
        color: #E6E8EC;
        background-color: $litepurp;
      }

      &:active {
        color: $darkpurp;
      }
    }
  }

  &__btn {
    display: flex;
    align-items: center;
  }

  &__btn-search {
    margin-right: 23px;

    @include _320 {
      margin-right: auto;
    }

    path {
      transition: all .3s ease-in-out;
    }

    &:hover path {
      stroke: $litepurp;
    }

    &:focus-visible path {
      stroke: $litepurp;
    }

    :active path {
      stroke: $darkpurp;
    }
  }

  &__btn-enter {
    padding: 10px 31.5px;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: #121723;
    border: 2px solid #121723;
    border-radius: 77px;
    transition: all .3s ease-in-out;

    @include _320 {
      padding: 7.5px 23px;
    }

    &:hover {
      color: #fff;
      background-color: #121723;
    }

    &:focus-visible {
      color: #121723;
      background-color: $litepurp;
    }

    &:active {
      color: #fff;
      background-color: $darkpurp;
      border-color: $darkpurp;
    }
  }

  &__bottom {
    border: 1px solid #E6E8EC;

    @include _1024 {
      padding: 10px 0;
    }

    @include _768 {
      padding: 15px 0;
    }

    @include _320 {
      padding: 13px 0;
    }
  }

  &__submenu-nav {
    @include _320 {
      display: none;
    }
  }

  &__submenu-list {
    display: flex;
  }

  &__submenu-item {
    &:not(:last-child) {
      margin-right: 52px;
    }

    @include _1024 {
      &:not(:last-child) {
        margin-right: 28px;
      }
    }

    @include _768 {
      &:not(:last-child) {
        margin-right: 29px;
      }

      &:first-child {
        margin-right: 31px;
      }
    }

    a {
      font-style: normal;
      font-weight: 400;
      font-size: 16px;
      line-height: 16px;
      color: #121723;
      transition: all .3s ease-in-out;

      &:hover {
        color: $purp;
      }

      &:focus-visible {
        color: #E6E8EC;
        background-color: $litepurp;
      }

      &:active {
        color: $darkpurp;
      }
    }
  }

  &__player {
    display: flex;
    border: 1px solid #E6E8EC;
    border-top: none;
    border-bottom: none;

    @include _768 {
      display: none;
    }
  }

  &__playlist-left,
  &__playlist-right {
    display:flex;
    border-right: 1px solid #E6E8EC;
  }

  &__playlist-left {
    padding: 3px 48px 4px 12px;

    @include _1024 {
      padding-right: 24.5px;
    }
  }

  &__playlist-right {
    padding: 3px 42px 4px 12px;

    @include _1024 {
      padding-right: 11px;
    }
  }

  &__title-right {
    display: flex;
    flex-direction: column;
  }

  &__btn-play {
    margin-right: 6px;

    @include _1024 {
      margin-right: 10px;
    }

    @include _768 {
      margin: auto;
    }

    @include _320 {
      display: flex;
      align-items: center;
      margin: auto;
      font-weight: 500;
      font-size: 16px;
      line-height: 16px;
      color: $purp;

      svg {
        margin-right: 5px;
      }
    }

    &:hover path {
      fill: $purp;
    }

    &:focus-visible path {
      fill: $litepurp;
    }

    &:active path {
      fill: $darkpurp;
    }
  }

  &__play {
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: #A1A6B4;
  }

  &__track {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #121723;
  }

  &__censore {
    display: flex;
    align-items: center;
    padding: 15px 8px;
    background-color: #E6E8EC;

    @include _1024 {
      display: none;
    }
  }

  &__cens-age {
    padding: 7px 4.5px;
    background-color: white;
    border-radius: 50%;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    color: #A1A6B4;
  }

  &__decoration--768 {
    display: none;

    @include _768 {
      display: block;
    }

    @include _320 {
      display:none;
    }
  }

  &__decoration--320 {
    display: none;

    @include _320 {
      display: flex;
      justify-content: center;
      width: 100%;
    }
  }
}

.btn-right {
  @include _1024 {
    margin-right: 2px;
  }
}
