.about {
  padding-top: 96px;
  padding-bottom: 93px;
  background-color: #121723;
  color: #A1A6B4;

  @include _320 {
    padding-top: 54px;
    padding-bottom: 54px;
  }

  &__top-content {
    display: flex;
    margin-bottom: 30px;

    @include _768 {
      flex-direction: column;
    }

    @include _320 {
      margin-bottom: 25px;
    }
  }

  &__left {
    max-width: 634px;
    margin-right: 29px;

    @include _768 {
      margin-bottom: 30px;
      margin-right: auto;
    }

    @include _320 {
      margin: auto;
    }
  }

  &__title {
    margin-bottom: 12px;
    color: #fff;

    @include _320 {
      font-weight: 400;
      font-size: 24px;
      line-height: 100%;
    }
  }

  &__subtitle {
    margin-bottom: 8px;
    font-weight: 700;
    font-size: 20px;
    line-height: 150%;
    color: #A1A6B4;

    @include _320 {
      margin-bottom: 4px;
      font-size: 14px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: #A1A6B4;

    @include _320 {
      font-weight: 400;
      font-size: 14px;
      line-height: 160%;
    }
  }

  &__right {
    position: relative;
    display: flex;
    align-items: center;

    @include _768 {
      justify-content: flex-end;
    }

    @include _320 {
      display: none;
    }
  }

  &__spiral {
    display: block;
    width: 160px;
    height: 290px;
    margin-right: 30px;
    background: url('/img/spiral.svg') center center no-repeat / cover;

    @include _1024 {
      display: none;
    }

    @include _768 {
      position: absolute;
      top: 0;
      right: 47px;
      display: block;
      background: url('/img/spiral_768.svg') center center no-repeat / contain;
      width: 639px;
      height: 112px;
    }
  }

  &__shine {
    display: block;
    width: 255px;
    height: 255px;
    background: url('/img/shine.svg') center no-repeat / cover;

    @include _768 {
      width: 112px;
      height: 112px;
    }
  }

  &__list {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px 30px;
    margin-bottom: 30px;

    @include _1024 {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 1fr);
    }

    @include _320 {
      gap: 15px 30px;
    }
  }

  &__item {
    min-height: 100%;
  }

  &__card {
    display: flex;
    flex-direction: column;
  }

  &__card-prew {
    margin-bottom: 30px;

    @include _320 {
      margin-bottom: 12px;
    }
  }

  &__card-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
  }

  &__guest-title {
    margin-bottom: 8px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: #fff;

    @include _320 {
      margin-bottom: 5px;
      font-size: 14px;
      line-height: 18px;
    }
  }

  &__guest-descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #A1A6B4;

    @include _320 {
      font-size: 12px;
      line-height: 16px;
    }
  }

  &__massage {
    max-width: 920px;
    width: 100%;
    height: 150px;
    margin-bottom: 25px;
    padding-top: 30px;
    padding-left: 32px;
    border-radius: 30px;

    @include _320 {
      padding-top: 17px;
      padding-left: 24px;
      border-radius: 20px;
    }
  }

  &__bottom-form {
    display: flex;
    align-items: center;
    max-width: 920px;
    margin-bottom: 30px;

    @include _768 {
      align-items: start;
      flex-direction: column;
    }
  }

  &__name,
  &__e-mail {
    display: flex;
    align-items: start;
    width: 100%;
    padding: 25px 32px;
    border-radius: 40px;

    @include _320 {
      padding: 17px 24px;
    }
  }

  &__form-btn {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 212px;
    margin-bottom: 30px;
    padding-top: 25px;
    padding-bottom: 25px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: #fff;
    background-color: $purp;
    border-radius: 40px;
    transition: all .3s ease-in-out;
    cursor: pointer;

    @include _320 {
      margin-bottom: 20px;
      width: 172px;
      padding-top: 17px;
      padding-bottom: 17px;
      font-size: 16px;
      line-height: 16px;
    }

    &:hover {
      background-color: $litepurp;
    }

    &:focus-visible {
      background-color: $litepurp;
      outline: 2px solid $darkpurp;
    }

    &:active {
      background-color: $darkpurp;
    }
  }

  &__check {
    display: flex;
    align-items: center;
    padding-top: 2px;
    padding-left: 21px;
    font-weight: 500;
    font-size: 12px;
    line-height: 12px;
    color: #fff;

    @include _320 {
      font-size: 9px;
      line-height: 9px;
    }
  }

  &__check-input {
    position: absolute;
    margin-left: -14px;
    margin-top: 7px;
    width: 1px;
    height: 1px;
    overflow: hidden;
  }

  &__check-box {
    position: absolute;
    margin-left: -22px;
    width: 14px;
    height: 14px;
    overflow: hidden;
    background-color: #121723;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    border: 1px solid #fff;
  }

  &:hover {
    border-color: $purp;
  }

  &__input-container,
  &__input-container-left {
    position: relative;
    flex: 1 1 auto;
  }

  &__input-container-left {
    margin-right: 30px;

    @include _768 {
      margin-right: auto;
      margin-bottom: 30px;
    }

    @include _320 {
      margin-bottom: 27px;
    }
  }
}

.is-invalid {
  border: 1px solid #FF6F6F;
}

.just-validate-error-label {
  position: absolute;
  z-index: 2;
  top: 10px;
  left: 30px;

  @include _320 {
    top: 6px;
    left: 25px;
    font-size: 10px;
  }
}

.about__check-input+.about__check-box {
  transition: all .3s ease-in-out;
}

.about__check-input:checked+.about__check-box {
  background-image: url('/img/check.svg');
}

.about__check-input:hover+.about__check-box {
  border-color: $purp;
}

.about__check-input:focus-visible+.about__check-box {
  border-color: $litepurp;
}

.about__check-input:active+.about__check-box {
  border-color: $darkpurp;
}
