.footer {
  padding-top: 94px;
  padding-bottom: 10px;
  background-color: #292E39;

  @include _320 {
    padding-top: 54px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    margin-bottom: 94px;

    @include _768 {
      align-items: center;
      flex-direction: column;
      margin-bottom: 80px;
    }

    @include _320 {
      margin-bottom: 10px;
    }
  }

  &__left {
    @include _768 {
      display: flex;
      align-items: center;
      flex-direction: column;
      margin-bottom: 80px;
    }

    @include _320 {
      margin-bottom: 270px;
    }
  }

  &__logo {
    display: block;
    margin-bottom: 20px;
  }

  &__social-list {
    display: flex;
    gap: 21px;
  }

  &__social-item {
    @include _768 {
      width: 36px;
      height: 36px;

      &:hover {
        svg {
          fill: $purp;
        }

      }

      svg {
        width: 100%;
        height: 100%;
      }
    }
  }

  &__social-link {
    path {
      transition: fill .3s ease-in-out;
    }

    &:hover {
      path {
        fill: $purp;
      }
    }

    &:focus-visible {
      path {
        fill: $litepurp;
      }
    }

    &:active {
      path {
        fill: $darkpurp;
      }
    }
  }

  &__right {
    @include _320 {
      position: absolute;
      display: flex;
      justify-content: center;
      width: 100%;
      margin-top: 130px;
      background-color: #121723;
    }
  }

  &__list {
    display: grid;
    grid-auto-flow: column;
    grid-template-rows: repeat(3, 1fr);
    row-gap: 20px;
    column-gap: 109px;

    @include _768 {
      column-gap: 157px;
      row-gap: 30px;
    }

    @include _320 {
      width: 320px;
      padding-top: 30px;
      padding-bottom: 30px;
      padding-left: 32px;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(5, 1fr);
      column-gap: 0;
      row-gap: 19px;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    transition: color .3s ease-in-out;

    @include _320 {
      font-size: 12px;
      line-height: 12px;
    }

    &:hover {
      color: $purp;
    }

    &:focus-visible {
      color: $litepurp;
    }

    &:active {
      color: $darkpurp;
    }
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__wave {
    display: block;
    width: 100%;
    height: 10px;
    margin-bottom: 10px;
    background: url('/img/footer-wave.svg') center center/ cover;
  }

  &__copyrights {
    font-weight: 400;
    font-size: 16px;
    line-height: 103.34%;
    color: #A1A6B4;

    @include _320 {
      font-size: 12px;
      line-height: 103.34%;
    }
  }
}
