//===========ШРИФТ ПО УМОЛЧАНИЮ===========
$fontfamily: "Muller", sans-serif;
//===========ШРИФТ ПО УМОЛЧАНИЮ===========


//===========ОБНУЛЕНИЕ СТИЛЕЙ===========
*{padding: 0;	margin: 0;	border: 0;}
*,*:before,*:after{-moz-box-sizing: border-box;	-webkit-box-sizing: border-box;	box-sizing: border-box;}
:focus,:active{outline: none;}
a:focus,a:active{outline: none;}
nav,footer,header,aside{display: block;}
html,body{font-family: $fontfamily; font-size: 100%;	line-height: 1;	font-size: 16px;	-ms-text-size-adjust: 100%;	-moz-text-size-adjust: 100%; -webkit-text-size-adjust: 100%;}
input,button,textarea{font-family:inherit;}
input::-ms-clear{display: none;}
button{cursor: pointer; font-family: $fontfamily; border: none; padding: 0; background-color: transparent;}
button::-moz-focus-inner {padding:0;border:0;}
a, a:visited{text-decoration: none; color: #fff;}
a:hover{text-decoration: none;}
ul li{list-style: none; margin: 0; padding: 0;}
img{vertical-align: top;}
h1,h2,h3,h4,h5,h6{font-size:inherit; font-weight: 400; margin: 0;}
img {max-width: 100%;}
