.transfers {
  padding: 92px 0;

  @include _768 {
    padding-bottom: 30px;
  }

  @include _320 {
    padding-top: 56px;
    padding-bottom: 23px;
  }

  &__container {
    display: flex;

    @include _768 {
      flex-direction: column;
    }
  }

  &__title {
    margin-bottom: 12px;

    @include _320 {
      margin-bottom: 20px;
      font-size: 24px;
    }
  }

  &__dev {
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #A1A6B4;

    @include _320 {
      margin-bottom: 4px;
      font-weight: 400;
      font-size: 12px;
      line-height: 12px;
    }
  }

  &__left {
    max-width: 255px;
    margin-right: 30px;

    @include _768 {
      margin: 0 0 47px 0;
    }

    @include _320 {
      margin-bottom: 30px;
    }
  }

  &__left-top {
    margin-bottom: 227px;

    @include _768 {
      margin-bottom: auto;
    }
  }

  &__left-bottom {
    display: flex;
    flex-direction: column;

    img {
      max-width: 65px;
      margin-bottom: 24px;
    }
  }

  &__btn {
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    max-width: 160px;
    padding: 9px 45px;
    border: 2px solid #121723;
    box-sizing: border-box;
    border-radius: 77px;
    font-weight: 500;
    font-size: 16px;
    line-height: 16px;
    color: #121723;
    transition: all .3s ease-in-out;

    &:hover {
      color: #fff;
      background-color: #121723;

      path {
        stroke: #fff;
      }
    }

    &:focus-visible {
      background-color: $litepurp;
    }

    svg {
      margin-right: 4px;

      path {
        transition: all .3s ease-in-out;
      }
    }

    &:active {
      color: #fff;
      background-color: $darkpurp;
      border-color: $darkpurp;

      path {
        stroke: #fff;
      }
    }

    @include _768 {
      margin-bottom: 8px;
      padding: 8px 44px;
    }

    @include _320 {
      width: 110px;
      padding: 7px 18px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: #A1A6B4;

    span {
      font-weight: 500;
    }

    @include _768 {
      width: 250px;
    }

    @include _320 {
      width: 166px;
      margin-bottom: 5px;
      font-size: 14px;
      line-height: 150%;
    }
  }

  &__right {
    position: relative;
    padding-top: 14px;

    @include _768 {
      margin-bottom: 58px;
      padding: 0;
    }

    @include _320 {
      margin-bottom: 27px;
    }
  }

  &__list {
    position: relative;
    display: grid;
    gap: 30px;
    grid-template-columns: repeat(3, 1fr);

    @include _1024 {
      grid-template-columns: 1fr 1fr;
      grid-template-rows: repeat(3, 1fr);
    }

    @include _320 {
      gap: 27px;
    }
  }

  &__item {
    border: 1px solid #E6E8EC;

    &:hover {
      .transfers__card-prew {
        opacity: .7;
      }

      .transfers__link-more {
        color: $purp;

        span {
          background-color: $purp;

          &:after {
            border-color: $purp;
          }
        }
      }
    }

    &:focus-visible {
      outline: 1px solid $litepurp;
      outline-offset: 7px;

      .transfers__link-more {
        color: #fff;
        background-color: $litepurp;

        span {
          background-color: #fff;

          &:after {
            border-color: #fff;
          }
        }
      }
    }

    &:active {
      .transfers__card-prew {
        opacity: .7;
      }

      .transfers__link-more {
        color: $darkpurp;

        span {
          background-color: $darkpurp;

          &:after {
            border-color: $darkpurp;
          }
        }
      }
    }
  }

  &__card {
    display: flex;
    flex-direction: column;
    max-width: 255px;
    width: 100%;
    height: 100%;
  }

  &__card-prew {
    transition: opacity .3s ease-in-out;
  }

  &__card-body {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px 14px;
    height: 100%;

    @include _320 {
      padding: 9px 11px 12px 9px;
    }
  }

  &__card-top {
    margin-bottom: 20px;

    @include _320 {
      margin-bottom: 17px;
    }
  }

  &__card-title {
    margin-bottom: 4px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    color: #121723;

    @include _320 {
      margin-bottom: 5px;
      font-size: 12px;
      line-height: 12px;
    }
  }

  &__event {
    font-weight: 400;
    font-size: 12px;
    line-height: 12px;
    color: #A1A6B4;

    @include _320 {
      font-size: 9px;
      line-height: 9px;
    }
  }

  &__link-more {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    font-style: italic;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #121723;
    transition: all .3s ease-in-out;

    @include _320 {
      font-size: 12px;
      line-height: 12px;
    }

    &:hover {
      color: $purp;

      span {
        background-color: $purp;

        &:after {
          border-color: $purp;
        }
      }
    }

    &:focus-visible {
      color: #fff;
      background-color: $litepurp;

      span {
        background-color: #fff;


        &:after {
          border-color: #fff;
        }
      }
    }

    &:active {
      color: $darkpurp;

      span {
        background-color: $darkpurp;

        &:after {
          border-color: $darkpurp;
        }
      }
    }
  }

  // &__botom {
  //   display: none;
  // }

  &__botom--visible {
    display: none;

    @include _768 {
      display: flex;
      justify-content: center;
      padding-top: 31px;
      border-top: 1px solid #E6E8EC;
    }

    @include _320 {
      padding-top: 28px;
    }
  }

  &__botom-left {
    @include _320 {
      display: flex;
      flex-direction: column-reverse;
    }
  }

  &__botom-img {
    margin-right: 20px;

    @include _320 {
      margin-right: 14px;
    }
  }
}

.choices {
  width: 255px;

  &__inner {
    position: relative;
    z-index: 2;
    min-height: 40px;
    padding: 12px 15.33px 12px 12px;
    border: 1px solid #E6E8EC;
    border-radius: 10px;
    background-color: #E6E8EC;
    font-weight: 400;
    font-size: 16px;
    line-height: 16px;
    color: #121723;
    transition: background .3s ease-in-out;

    &:hover {
      background: rgba(230, 232, 236, 0.4);
      border-color: rgba(230, 232, 236, 0.4);
    }

    &:active {
      background: #D2D5DD;
    }
  }

  &__list--single {
    padding: 0;
  }
}

.is-focused .choices__inner {
  border: 1px solid #6D31EE;
}

.is-open .choices__inner {
  border-radius: 10px;
  border: 1px solid #D2D5DD;
}

.choices[data-type*=select-one]::after {
  content: '';
  position: absolute;
  z-index: 2;
  top: 20px;
  right: 15px;
  display: block;
  width: 11px;
  height: 11px;
  border-top: 1px solid #A1A6B4;
  border-left: 1px solid #A1A6B4;
  transform: translateY(-50%) rotate(-135deg);
  transition: .2s ease-in-out;
}

.choices[data-type*=select-one].is-open::after {
  margin: auto;
  border-color: transparent;
  border-top: 1px solid #A1A6B4;
  border-left: 1px solid #A1A6B4;
  transform: rotate(45deg);
  transition: all .3s ease-in-out;
}

.choices__list--dropdown,
.choices__list[aria-expanded] {
  z-index: 1;
  top: 20px;
  padding-top: 20px;
  border-top: none;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.is-open .choices__list--dropdown {
  border-color: #D2D5DD;
}

.choices__list--dropdown .choices__item,
.choices__list[aria-expanded] .choices__item {
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #A1A6B4;

  &:not(:last-child) {
    border-bottom: 1px solid #D2D5DD;
  }
}


.choices__item--selectable.is-highlighted,
.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  color: #121723;
  background: #AA9DFA;
}

.choices__list--dropdown .choices__item--selectable,
.choices__list[aria-expanded] .choices__item--selectable {
  padding: 12px;
}

.visible {
  display: none;

  @include _768 {
    display: flex;
    justify-content: center;
  }
}

.unvisible {
  @include _768 {
    display: none;
  }
}
