.hamb {
  display: none;

  @include _768 {
    display: flex;
    align-items: center;

    &:focus-visible {
      outline: 2px solid red;
    }

    &__field {
      cursor: pointer;
    }
  }
}

.popup {
  display: none;

  @include _768 {
    position: fixed;
    top: 146px;
    left: 100%;
    width: 100%;
    height: 100%;
    background-color: white;
    z-index: 100;
    display: flex;
    transition: all .3s ease-in-out;
  }
}

@media (max-width: 855px) {
  .header__container .header__menu-list {
    display: none;
  }

  .bar {
    display: block;
    width: 24px;
    height: 3px;
    background-color: $purp;
    transition: 0.2s;

    &:not(:last-child) {
      margin-bottom: 7px;
    }
  }

  .popup.open {
    left: 0;
  }

  .popup .header__menu-list {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: start;
    padding: 50px 0;
    overflow: auto;
    margin: auto;
  }

  .popup .header__menu-list > li {
    width: 100%;
    margin-right: auto;
  }

  .popup .header__menu-list > li > a {
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 20px 0;
    font-size: 20px;
    font-weight: bold;
    color: #3f3f3f;
  }

  .popup .header__menu-list > li > a:hover {
    background-color: rgba(122, 82, 179, 0.1);
  }

  .hamb__field.active .bar:nth-child(2) {
    opacity: 0;
  }

  .hamb__field.active .bar:nth-child(1) {
    transform: translateY(10px) rotate(45deg);
  }

  .hamb__field.active .bar:nth-child(3) {
    transform: translateY(-10px) rotate(-45deg);
  }

  body.noscroll {
    overflow: hidden;
  }
}
