$purp: #6D31EE;
$litepurp: #AA9DFA;
$darkpurp: #4F28A5;

.title {
  font-weight: 400;
  font-size: 48px;
  line-height: 100%;
  color: #121723;
}

.element--hidden {
  @include _320 {
    display: none;
  }
}

.btn-wrapper {
  display: flex;
  justify-content: center;
}

.arrow {
  position: relative;
  width: 20px;
  height: 1px;
  margin-left: 4px;
  background-color: #121723;
  transition: all .3s ease-in-out;

  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 6px;
    height: 6px;
    border-top: 1px solid #121723;
    border-left: 1px solid #121723;
    transform: translateY(-50%) rotate(135deg);
    transition: all .3s ease-in-out;
  }
}

.rus-lable {
  background: url('/img/rus.svg') no-repeat center center;
}

.eng-lable {
  background: url('/img/eng.svg') no-repeat center center;
}

.form-style {
  background-color: #121723;
  border: 1px solid #fff;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  color: #fff;
  resize: none;

  &:hover {
    background-color: rgba(230, 232, 236, 0.1);
  }

  &:focus-visible {
    background-color: rgba(230, 232, 236, 0.1);
    border-color: $litepurp;
  }

  @include _320 {
    font-size: 16px;
    line-height: 16px;
  }
}
